.form_wrapper {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-template-rows: repeat(2, 1fr) 0.5fr repeat(2, 1fr) 0.5fr repeat(3, 1fr);
  grid-column-gap: 0;
  grid-row-gap: 0;
  margin-top: 45px;

  & .form_city {
    grid-area: 1 / 1 / 2 / 2;
  }

  & .form_uni {
    grid-area: 2 / 1 / 3 / 2;
  }

  & .form_city_select {
    grid-area: 1 / 2 / 2 / 3;
  }

  & .form_uni_select {
    grid-area: 2 / 2 / 3 / 3;
  }

  & .div5 {
    grid-area: 3 / 1 / 4 / 6;
    border-bottom: 1px solid #E5E4E4;
    margin-bottom: 40px;
  }

  & .div6 {
    grid-area: 4 / 1 / 5 / 2;
  }

  & .div7 {
    grid-area: 4 / 2 / 5 / 3;

    & input {
      padding-left: 10px;
      font-size: 16px;
      outline: #666666;
      width: 336px;
      height: 32px;
      border: 1px solid #999999;
    }
  }

  & .div8 {
    grid-area: 4 / 3 / 5 / 4;
    font-size: 12px;
    color: #999999;
  }

  & .div9 {
    grid-area: 5 / 1 / 6 / 2;
  }

  & .div10 {
    grid-area: 5 / 2 / 6 / 3;

    & input {
      padding-left: 10px;
      font-size: 16px;
      outline: #666666;
      width: 336px;
      height: 32px;
      border: 1px solid #999999;
    }
  }

  & .div11 {
    grid-area: 5 / 3 / 6 / 4;
    font-size: 12px;
    color: #999999;
  }

  & .div12 {
    grid-area: 6 / 1 / 7 / 4;
    border-bottom: 1px solid #E5E4E4;
    margin-bottom: 40px;
  }

  & .div13 {
    grid-area: 7 / 1 / 8 / 2;
  }

  & .div14 {
    grid-area: 7 / 2 / 8 / 3;

    & input {
      padding-left: 10px;
      font-size: 16px;
      outline: #666666;
      width: 336px;
      height: 32px;
      border: 1px solid #999999;
    }
  }

  & .div15 {
    grid-area: 7 / 3 / 8 / 4;
    font-size: 12px;
    color: #999999;
  }

  & .div16 {
    grid-area: 8 / 1 / 9 / 2;
  }

  & .div17 {
    grid-area: 8 / 2 / 9 / 3;
  }

  & .div18 {
    grid-area: 9 / 2 / 10 / 3;
    display: inline-block;
    font-size: 12px;
    color: #999999;
  }

  span {
    font-size: 21px;
  }
}